import React, { useState } from 'react';
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import img from "./profile.png";
import Avatar from 'react-avatar-edit';
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';

const Profile = () => {
  const [dialogs, setdialogs] = useState(false);
  const [imgCrop, setimgCrop] = useState(null);
  const [storeImage, setstoreImage] = useState([]);

  const onCrop = (view) => {
    setimgCrop(view);
  };

  const onClose = () => {
    setimgCrop(null);
  };

  const saveImage = async () => {
    try {
      const formData = new FormData();
      formData.append('image', setstoreImage([...storeImage, { imgCrop }])); // Use 'imgCrop.blob' instead of 'imgCrop.file'
      const csrfToken = document.cookie
        .split('; ')
        .find(cookie => cookie.startsWith('csrftoken'))
        .split('=')[1];

      const response = await axios.post('http://127.0.0.1:8000/api/update-profile-image', formData, {
        method:'POST',
        headers: {
          'Content-Type': 'multipart/form-data',
          'X-CSRFToken': csrfToken,
        },
      });

      if (response.status === 200) {
        setstoreImage([...storeImage, { imgCrop }]);
        setdialogs(false);
      } else {
        // Handle error
      }
    } catch (error) {
      // Handle error
    }
  };

  const profileImageShow = storeImage.map((item) => item.imgCrop);

  return (
    <div className='profile_img text-center p-4'>
      <div className='flex flex-column justify-content-center align-item-center'>
        <img
          style={{
            width: "200px",
            height: "200px",
            borderRadius: "50%",
            objectFit: "cover",
          }}
          src={profileImageShow.length ? profileImageShow : img}
          alt=''
          onClick={() => setdialogs(true)}
        />
        

        <Dialog
          visible={dialogs}
          header={() => (
            <p className='text-2x1 front-semibold textColor'>
              Update Profile
            </p>
          )}
          onHide={() => setdialogs(false)}
        >
          <div className="confirmation-content flex flex-column align-items-center">
            <div className="flex flex-column align-items-center mt-5 w-12">
              <div className="flex flex-column justify-content-around w-12 mt-4">
                <Avatar width={400} height={300} onClose={onClose} onCrop={onCrop} />
                <Button onClick={saveImage} label="Save" icon="pi pi-check" />
              </div>
            </div>
          </div>
        </Dialog>
      </div>
    </div>
  );
};

const App = () => {
  return (
    <div>
      <h1>Profile Page</h1>
      <Profile />
    </div>
  );
};

export default App;
